html {
  scroll-behavior: smooth;
  background-color: #1f2a36;
  color: #ecf0f1;
  font-family: "Space Mono", monospace;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

html::-webkit-scrollbar {
  width: 0.5vw;
}

html::-webkit-scrollbar-thumb {
  background-color: #394f63;
}

html::-webkit-scrollbar-thumb:hover {
  background-color: #394f63;
}

html::-webkit-scrollbar-track {
  background-color: #5555;
}

html::-webkit-scrollbar-track:hover {
  background-color: #5555;
}

@font-face {
  font-family: "SpaceMono";
  src: url("../public/assets/SpaceMono-Bold.ttf");
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
}

section {
  max-width: 60%;
}

header {
  position: sticky;
  top: 0;
  padding: 0 2rem;
  background-color: #2c3e50;
  border-bottom: 2px solid #394f63;
  z-index: 100;
}

.navbar {
  max-width: 100%;
  min-height: 60px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.navbar .links {
  display: flex;
  gap: 2rem;
}

/* Logo styles */
.navbar .rat {
  object-fit: cover;
  height: 4rem;
  width: 4rem;
}

.toggle_btn {
  color: #f1ede1;
  cursor: pointer;
  display: none;
}

.action_btn {
  background-color: #7a8f91;
  border: 2px solid #95a5a6;
  color: #f1ede1;
  padding: 0.5rem 1rem;
  outline: none;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: bold;
  cursor: pointer;
  transition: scale 0.2 ease;
}

.action_btn:hover {
  scale: 1.05;
  color: #f1ede1;
}

.action_btn:active {
  scale: 0.9;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  font-size: 1rem;
  color: #f1ede1;
}

/* Global link styles */
a:hover {
  color: #cfc8b0;
}

.dropdown_menu {
  display: none;
  width: 100%;
  background: #394f63;
  backdrop-filter: blur(15px);
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 1rem;
}

.dropdown_menu ul {
  margin-left: -2.5rem;
}

nav#dropdownMenu {
  padding-bottom: 1rem;
}

.dropdown_menu li {
  padding: 0.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dropdown_menu .action_btn {
  margin-left: 27%;
  width: 40%;
  font-size: 1rem;
  display: flex;
  justify-content: center;
}

.frontpage {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.start {
  display: flex;
  flex-direction: column;
  max-width: 50%;
  margin-top: 3rem;
}

.hi {
  margin-top: 4rem;
  text-align: left;
  font-size: 3rem;
}

.story {
  text-align: left;
  font-size: 1.5rem;
}

.me {
  margin-top: 3rem;
  margin-left: 8rem;
  transform: translateX(-3rem);
  border: 0.2rem solid #95a5a6;
  border-radius: 29% 71% 52% 48% / 49% 61% 39% 51%;
  transition: border-radius 750ms ease-in-out;
}

.me:hover {
  border-radius: 54% 46% 31% 69% / 36% 61% 39% 64%;
}

.medias {
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  transform: translate(25rem, -15rem);
}

#mob1 {
  display: none;
}

#mob2 {
  display: none;
}

.frontpage i {
  color: #cfc5c5;
  font-size: 1.5rem;
}

.frontpage i:hover {
  animation: bounce 1s;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-15px);
  }

  60% {
    transform: translateY(-8px);
  }
}

/* about section */
#about-area {
  display: flex;
  flex-wrap: wrap;
}

.single-blog {
  min-width: 23rem;
  max-width: 25rem;
  transition: all 0.3s ease 0s;
}

.single-blog:hover {
  transform: translateY(-10px);
}

.single-blog .post-img {
  position: relative;
}

.single-blog .post-img img {
  width: 100%;
  object-fit: cover;
  border-radius: 1rem;
}

.single-blog .post-img:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  border-radius: 1rem;
  background: rgba(81, 157, 187, 0.6);
  transition: all 0.3s ease 0s;
}

.single-blog:hover .post-img:after {
  opacity: 1;
}

.single-blog .single_blog {
  border-top: none;
  padding: 25px 20px 25px;
  position: relative;
}

.post-title {
  text-align: center;
  display: flex;
  justify-content: center;
  font-size: 20px;
  text-transform: capitalize;
  transition: all 0.3s ease 0s;
}

.single-blog .blog-text {
  text-align: center;
  font-size: 15px;
  line-height: 23px;
  margin: 7px 0 10px;
}

h1 {
  font-size: 2rem;
}

.tools {
  display: flex;
  flex-wrap: wrap;
  margin-left: 7.7rem;
  padding: 1rem;
  gap: 1rem;
  font-size: 5rem;
  color: #ffffff;
  max-width: 70%;
}

.tool {
  transform: translate(0rem, 1.2rem);
  height: auto;
  max-width: 5rem;
}

.proje {
  display: flex;
  justify-content: space-around;
  margin-bottom: 8rem;
}

.proje #project {
  margin: 1rem;
  max-width: 26rem;
  border: 0.5rem solid #395068;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

#project:hover {
  transform: translateY(-1rem);
}

.projects p {
  font-size: 0.8rem;
  margin-left: 1rem;
  margin-top: 2.5rem;
  font-size: 1.2rem;
}

.textt {
  display: block;
  max-width: 26rem;
}

.btn1 {
  display: inline;
  margin-left: 2.8rem;
  position: relative;
  border-bottom: 2px solid #395068;
}

.btn1:hover {
  color: #fff;
}
.btn1::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #395068;
  z-index: -1;
  transition: transform 0.5s;
  transform-origin: 0 0;
  transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
}
.btn1::before {
  transform: scaleX(0);
}
.btn1:hover::before {
  transform: scaleX(1);
}

#flipped #project {
  margin: 1rem;
  max-width: 26rem;
  border: 0.3rem solid #395068;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

#flipped {
  display: flex;
  margin-top: -5rem;
}

#flipped .textt {
  display: block;
  max-width: 26rem;
  font-size: 0.8rem;
}

.cheeze {
  max-width: 26rem;
}

.printImg {
  max-width: 100%;
  border: 4px solid #394f63;
  opacity: 0.75;
  transition: opacity 0.3s, transform 0.3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.printImg:hover {
  opacity: 1;
  transform: scale(1.2) translateY(10px);
}

.printing {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.pImg {
  max-width: 18rem;
  object-fit: cover;
  margin: 1rem;
  border: 2px solid #394f63;
  opacity: 0.75;
  transition: opacity 0.3s, transform 0.3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.pImg:hover {
  opacity: 1;
  transform: scale(1.5) translateY(-5px);
  z-index: 100;
}

